import React, { useState } from 'react';
import { fetchChatCompletion } from '../api/deepseek';
import Logout from './Logout'; // 确保这是默认导入
import {
  Container,
  Box,
  TextField,
  MenuItem,
  Button,
  Typography,
  AppBar,
  Toolbar,
  Select,
  FormControl,
  InputLabel,
} from '@mui/material';

const MainApp = ({ user }) => {
  const [inputText, setInputText] = useState('');
  const [outputText, setOutputText] = useState('');
  const [mode, setMode] = useState('business');

  const translateText = async () => {
    const systemPrompt = mode === 'business'
      ? 'You are a professional assistant in a business environment.'
      : 'You are a friendly assistant chatting casually.';

    const messages = [
      { role: 'system', content: systemPrompt },
      { role: 'user', content: inputText },
    ];
    try {
      const response = await fetchChatCompletion(messages);
      setOutputText(response);

      if (window.gtag) {
        window.gtag('event', 'translate', {
          event_category: 'Text Translation',
          event_label: mode,
          value: inputText.length,
        });
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  return (
    <Container>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            SoundNative
          </Typography>
          <Logout /> {/* 确保导入的组件名称与导出一致 */}
        </Toolbar>
      </AppBar>
      <Box sx={{ my: 4 }}>
        <Typography variant="h5" component="div" gutterBottom>
          Welcome, {user.email}
        </Typography>
        <Box>
          <TextField
            label="Enter your text here..."
            multiline
            fullWidth
            rows={4}
            value={inputText}
            onChange={(e) => setInputText(e.target.value)}
            variant="outlined"
            margin="normal"
          />
          <FormControl fullWidth margin="normal">
            <InputLabel id="mode-select-label">Mode</InputLabel>
            <Select
              labelId="mode-select-label"
              value={mode}
              label="Mode"
              onChange={(e) => setMode(e.target.value)}
            >
              <MenuItem value="business">Business</MenuItem>
              <MenuItem value="casual">Casual</MenuItem>
            </Select>
          </FormControl>
          <Button variant="contained" color="primary" onClick={translateText} fullWidth>
            Translate
          </Button>
          <TextField
            label="Your translated text will appear here..."
            multiline
            fullWidth
            rows={4}
            value={outputText}
            InputProps={{
              readOnly: true,
            }}
            variant="outlined"
            margin="normal"
            sx={{ mt: 2 }}
          />
        </Box>
      </Box>
    </Container>
  );
};

export default MainApp;
